.ContactFormContainer {
    position: relative;
    overflow: hidden;

    >img {
        position: absolute;
        top: 16%;
        z-index: -1;
        width: 100vw;
    }
}

.formAboutContainer {
    padding: 5%;
}

.formContainer {
    display: flex;
    width: 100%;
    min-height: 35vw;

    >form {
        display: flex;
        flex-direction: column;
        /* justify-content: space-around; */
        width: 60%;
        /* border: 1px solid #828282; */
        border-radius: 0.554vw;
        padding: 1% 1.5%;
        height: fit-content;
        background-color: white;
        box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;

        >input {
            border: none;
            border-bottom: 1px solid #828282;
            padding: 3% 0;
            margin: 2% 0;
            /* font-family: Poppins; */
            font-size: 1.386vw;
            font-weight: 500;
            line-height: 2.079vw;
        }

        >button {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #051052;
            color: white;
            padding: 0.970vw 0.832vw;
            border: none;
            border-radius: 0.277vw;
            gap: 0.693vw;
            /* font-family: Poppins; */
            font-size: 1.109vw;
            font-weight: 500;
            line-height: 1.663vw;
            margin: 10% 0;

            >img {
                height: 1.663vw;
                width: 1.663vw;
            }
        }
    }
}

.formFeedback {
    margin-top: 10%;
}

::placeholder {
    color: #B3B3B3;
}



.formHeader {
    width: 40%;
    padding-right: 4%;

    /* font-family: Poppins; */
    >h2 {
        font-size: 2.772vw;
        font-weight: 600;
        line-height: 4.158vw;
        color: #111111;
        margin-bottom: 2%;
    }

    >p {
        /* font-family: Poppins; */
        font-size: 1.386vw;
        font-weight: 500;
        line-height: 2.079vw;
        color: #666666;
    }
}

.aboutContainer {
    box-sizing: border-box;
    /* background-image: url('../../../Assets/aboutBg.webp'); */
    background: linear-gradient(90deg, rgba(212,65,23,0.7988445378151261) 24%, rgba(84,24,207,0.8016456582633054) 100%);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 0.832vw;
    margin-top: 5%;
    padding: 5% 15%;
    display: flex;
    flex-direction: column;
    min-height: 61.538vw;

}

.abouts {
    color: #FFFFFF;
    margin-bottom: 3.326vw;
    display: flex;
    flex-direction: column;

    >h2 {
        /* font-family: Poppins; */
        font-size: 3.326vw;
        font-weight: 600;
        line-height: 4.712vw;
        text-align: center;
        margin-bottom: 1.386vw;
    }
}

.trust {
    color: #FFFFFF;
    font-size: 1.663vw;
    font-weight: 500;
    line-height: 2.079vw;
    text-align: center;
}

.aboutDetail {
    /* font-family: Poppins; */
    font-size: 1.386vw;
    font-weight: 400;
    line-height: 2.079vw;
    text-align: center;
    color: #FFFFFFD9;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.aboutSubHeading {
    color: #FFFFFF;
    /* font-family: Poppins; */
    font-size: 2.495vw;
    font-weight: 500;
    line-height: 3.742vw;
    text-align: center;
    margin-bottom: 0.832vw;
}

.aboutList {
    text-align: center;
    width: fit-content;

    >a{
        text-decoration: none;
        color: #FFFFFFD9;
    }

    >a:hover{
        text-decoration: underline;
    }
}

